<template>
<transition name="slide-fade">
	<div class="cc-main">
		<img class="cc-logo" src="../assets/img/bg.png" alt="">
		<div class="cc-jieshao" @click="goto('aboutus')">平台介绍</div>
		<div class="cc-register">
			<div class="cc-register1">
				<div class="cc-tabaddr">
					<div class="cc-l" :class="{active:isactive}" @click="show = true">
						<div>{{district}}</div>
						<span>{{province}}/{{city}}</span>
					</div>
					<div class="cc-c" @click="tabaddr"><img src="../assets/img/tabaddr.png" alt=""></div>
					<div class="cc-r" :class="{active:isactive}" @click="show2 = true">
						<div>{{district2}}</div>
						<span>{{province2}}/{{city2}}</span>
					</div>
				</div>
				<div class="cc-date flexstart" @click="showPicker1 = true">
					<span>出发日期</span>
					<div>{{currentDate1}}</div>
					<span>{{week}}</span>
					<van-icon class="cc-icon3" color="#C5CAD5" name="arrow" />
				</div>
				<div class="cc-ordernum spacebetween">
					<div class="flexstart">{{currentDate1.split('-')[0]}}年{{currentDate1.split('-')[1]}}月{{currentDate1.split('-')[2]}}日 预约人数已达<span>{{num}}</span>人</div>
					<div>满50人发车</div>
				</div>
			</div>
			<div class="cc-register1">
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="username" placeholder="请输入您的真实姓名" label="您的姓名" /></div>
				<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="userid" placeholder="请输入身份证号" label="身份证号" maxlength="18" /></div>
				<div class="cc-input0 cc-noborder"><van-field class="cc-input" label-width="84px" v-model="usertel" type="tel" clearable placeholder="请输入手机号码" label="手机号码" /></div>
			</div>
			<div class="cc-register1">
				<van-tabs v-model="active" color="#1E97A8">
					<van-tab title="我要求职">
						<div class="cc-input0"><van-field class="cc-input" readonly clickable label="学历" :value="education1" placeholder="请选择您的学历" right-icon="arrow" @click="showPicker = true"/></div>
						<van-field class="cc-input" label-width="84px" v-model="des" type="textarea" autosize placeholder="请输入相关工作经历、技能等" label="备注" />
					</van-tab>
					<van-tab title="我已在职">
						<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="enterprise" placeholder="请输入所在企业名称" label="企业名称" /></div>
						<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="companyDistrict" placeholder="请选择所在企业所在区" label="企业地区" readonly right-icon="arrow" @click="show1 = true" /></div>
						<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="enterprisename" placeholder="请输入所在企业联系人姓名" label="联系人" /></div>
						<div class="cc-input0"><van-field class="cc-input" label-width="84px" v-model="enterprisetel" type="tel" clearable placeholder="请输入所在企业联系人手机号码" label="手机号码" /></div>
						<van-field class="cc-input" label-width="84px" v-model="des" type="textarea" :autosize="{maxHeight:62}" placeholder="请输入相关工作经历、技能等" label="备注" />
					</van-tab>
				</van-tabs>
			</div>
			<div class="cc-button" @click="sub(1)" v-if="issign==1">修改登记</div>
			<div class="cc-button" @click="sub(0)" v-else>确认登记</div>
			<div class="cc-button b2" @click="goto('list')">跳过，浏览推荐岗位</div>
		</div>
		<img class="cc-logo1" src="../assets/img/logo1.png" alt="">
		<van-popup v-model="show" position="bottom">
			<van-area :area-list="areaList" :value="districtCode" visible-item-count="9" @confirm="confirmarea" @cancel="show = false" />
		</van-popup>
		<van-popup v-model="show2" position="bottom">
			<van-area :area-list="areaList" :value="districtCode2" visible-item-count="9" @confirm="confirmarea2" @cancel="show2 = false" />
		</van-popup>
		<van-popup v-model="show1" position="bottom">
			<van-area :area-list="areaList" :value="companyDistrictCode" visible-item-count="9" @confirm="confirmarea1" @cancel="show1 = false" />
		</van-popup>
		<van-popup v-model="showPicker" position="bottom">
			<van-picker show-toolbar :columns="columns" @cancel="showPicker = false" @confirm="onConfirm" />
		</van-popup>
		<van-popup v-model="showPicker1" position="bottom">
			<van-datetime-picker v-model="currentDate" type="date" :min-date="minDate" @cancel="showPicker1 = false" @confirm="onConfirm1" />
		</van-popup>
	</div>
</transition>
</template>
<script type="text/javascript">
import {Area ,Popup ,Field ,Switch ,Cell,Picker,Icon,Toast,Dialog ,Tab, Tabs,DatetimePicker } from 'vant';
import areaList from '@/assets/js/area.js'//基础样式
export default {
	name: 'home',
	components: {
		[Area .name]: Area ,
		[Field.name]: Field,
		[Popup.name]: Popup,
		[Switch.name]: Switch,
		[Cell.name]: Cell,
		[Picker.name]: Picker,
		[Icon.name]: Icon,
		[Dialog.name]: Dialog,
		[Toast.name]: Toast,
		[Tab.name]: Tab,
		[Tabs.name]: Tabs,
		[DatetimePicker.name]: DatetimePicker,
	},
	data() {
		return {
			areaList,
			isactive:false,
			istab:true,
			show:false,
			districtCode:'341202',
			province:"安徽省",
			city:"阜阳市",
			district:"颍州区",
			show2:false,
			districtCode2:'330203',
			province2:"浙江省",
			city2:"宁波市",
			district2:"海曙区",

			showPicker1: false,
			minDate: new Date(),
			currentDate: new Date(),
			currentDate1: "",
			week:"",
			num:0,

			show1:false,
			companyDistrictCode:'330203',
			companyDistrict:"海曙区",
			username:"",
			userid:"",
			usertel:"",
			enterprise:"",
			enterprisename:"",
			enterprisetel:"",
			des:"",
			education:"",
			education1:"",
			showPicker: false,
			columns: ['请选择','初中及以下', '中专/技校', '高中', '大专', '本科', '本科以上'],//1-初中及以下，2-中专/技校，3-高中，4-大专，5-本科，6-硕士
			checked:true,
			active:0,
			issign:0
		};
	},
	mounted() {
		this.issign=localStorage.getItem("tgbissign1");
		this.currentDate1=this.formatTime(new Date());
		this.week=this.getWeekDate(new Date());
		this.getIntermediaryInfo();
	},
	methods: {
		goto(rt){
			this.$router.push({
				path:"/"+rt
			})
		},
		tabaddr(){
			var that=this;
			if (this.istab) {
				this.istab=false;
				this.isactive=!this.isactive;
				setTimeout(function(){that.istab=true;}, 1000);
			}
		},
		//渲染数据
		getIntermediaryInfo(){
			var that=this;
			this.$http.get('/main/getIntermediaryInfo').then(function (res) {
				if (res.code==10000) {
					if (res.data.person) {
						that.username=res.data.person.name;
						that.userid=res.data.person.idNum;
						that.usertel=res.data.person.mobile;
						that.districtCode=res.data.person.districtCode;
						that.province=res.data.person.province;
						that.city=res.data.person.city;
						that.district=res.data.person.district;
						if (res.data.person.destinationCode) {
							that.districtCode2=res.data.person.destinationCode;
						}
						if (res.data.person.destinationProvince) {
							that.province2=res.data.person.destinationProvince;
						}
						if (res.data.person.destinationCity) {
							that.city2=res.data.person.destinationCity;
						}
						if (res.data.person.destinationDistrict) {
							that.district2=res.data.person.destinationDistrict;
						}
						if (res.data.person.returnDate) {
							that.currentDate1=res.data.person.returnDate;
						}
						if (res.data.person.returnWeek) {
							that.week=res.data.person.returnWeek;
						}
						that.companyDistrictCode=res.data.person.companyDistrictCode;
						that.companyDistrict=res.data.person.companyDistrict;
						that.enterprise=res.data.person.companyName;
						that.enterprisename=res.data.person.contact;
						that.enterprisetel=res.data.person.contactMobile;
						that.des=res.data.person.description;
						that.education=res.data.person.education;
						that.education1=that.columns[res.data.person.education];
						that.checked=res.data.person.isOffer;
						if (res.data.person.isOffer) {
							that.active=1
						}else{
							that.active=0
						}
					}
					that.getAppointmentCount();
				}
			}).catch(function (error) {
				console.log(error);
			});
		},
		getAppointmentCount(){
			var that=this;
			this.$http.get(`/main/getAppointmentCount?code=${that.districtCode}&destinationCode=${that.districtCode}&date=${that.currentDate1}`).then(function (res) {
				if (res.code==10000) {
					that.num=res.data
				}
			}).catch(function (error) {
				console.log(error);
			});
		},
		//老家所在地选择
		confirmarea(res){
			this.province=res[0].name;
			this.city=res[1].name;
			this.district=res[2].name;
			this.districtCode=res[2].code;
			this.show = false;
			this.getAppointmentCount();
		},
		confirmarea2(res){
			this.province2=res[0].name;
			this.city2=res[1].name;
			this.district2=res[2].name;
			this.districtCode2=res[2].code;
			this.show2 = false;
			this.getAppointmentCount();
		},

		//公司所在区域
		confirmarea1(res){
			this.companyDistrictCode=res[2].code;
			this.companyDistrict=res[2].name;
			this.show1 = false;
		},
		//学历选择
		onConfirm(res, index){
			this.showPicker = false;
			this.education1=res;
			this.education=index;
		},
		onConfirm1(val){
			this.showPicker1 = false;
			this.currentDate1=this.formatTime(val);
			this.week=this.getWeekDate(val);
			this.getAppointmentCount();
		},
		formatTime(date){
			const year = date.getFullYear()
			const month = date.getMonth() + 1
			const day = date.getDate()
			return `${year}-${formatNumber(month)}-${formatNumber(day)}`
			function  formatNumber (n) {
				n = n.toString()
				return n[1] ? n : '0' + n
			}
		},
		getWeekDate(now) {
			var day = now.getDay();
			var weeks = new Array("星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六");
			var week = weeks[day];
			return week;
		},
		//提交数据
		sub(a){
			let test1 = /^1[3-9][0-9]{9}$/
			if (!test1.test(this.usertel)) {
				Toast.fail("请填写正确格式的手机号码");
				return false;
			}
			var that=this;
			if (that.active==1) {
				that.checked=true;
			}else{
				that.checked=false;
			}
			this.$http.post('/main/workRegister',{
				name:that.username,
				idNum:that.userid,
				mobile:that.usertel,
				province:that.province,
				city:that.city,
				district:that.district,
				districtCode:that.districtCode,
				destinationProvince:that.province2,
				destinationCity:that.city2,
				destinationDistrict:that.district2,
				destinationCode:that.districtCode2,
				returnDate:that.currentDate1,
				returnWeek:that.week,
                isOffer:that.checked,
                companyName:that.enterprise,
                companyDistrict:that.companyDistrict,
                companyDistrictCode:that.companyDistrictCode,
                education:that.education,
                contact:that.enterprisename,
                contactMobile:that.enterprisetel,
                description:that.des
			}).then(function (res) {
				if (res.code==10000) {
					localStorage.setItem("tgbissign1",1);
					that.issign=1;
					if (a==1) {
						Dialog.alert({
							title: '提示',
							message: '您已修改成功，我们会尽快与您联系，请保持手机畅通！',
							confirmButtonText:'我知道了'
						}).then(() => {
							that.$router.push({
								path:"/list"
							})
						});
					}else{
						Dialog.alert({
							title: '提示',
							message: '您已登记成功，我们会尽快与您联系，请保持手机畅通！',
							confirmButtonText:'我知道了'
						}).then(() => {
							that.$router.push({
								path:"/list"
							})
						});						
					}
				}else{
					Toast.fail(res.msg);
				}
			}).catch(function () {
			});
		},
	}
}
</script>
